.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #9baacf;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-tree {
  position: relative;
  cursor: default;
  color: #8394be; }
  .tu-tree__empty-block {
    position: relative;
    min-height: 60px;
    text-align: center;
    width: 100%;
    height: 100%; }
  .tu-tree__empty-text {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: #bec8e4;
    font-size: 14px; }
  .tu-tree__drop-indicator {
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
    background-color: #6d5dfc; }

.tu-tree-node {
  white-space: nowrap;
  outline: none; }
  .tu-tree-node:focus > .tu-tree-node__content {
    color: #6d5dfc; }
  .tu-tree-node.is-drop-inner > .tu-tree-node__content .tu-tree-node__label {
    color: #6d5dfc; }
  .tu-tree-node__content {
    display: flex;
    align-items: center;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    transition: background-color 0.3s; }
    .tu-tree-node__content > .tu-tree-node__expand-icon {
      font-size: 13px;
      padding: 8px 6px; }
    .tu-tree-node__content > label.tu-checkbox {
      margin-right: 8px; }
    .tu-tree-node__content:hover {
      color: #6d5dfc; }
    .tu-tree.is-dragging .tu-tree-node__content {
      cursor: move; }
      .tu-tree.is-dragging .tu-tree-node__content * {
        pointer-events: none; }
    .tu-tree.is-dragging.is-drop-not-allow .tu-tree-node__content {
      cursor: not-allowed; }
  .tu-tree-node__expand-icon {
    cursor: pointer;
    color: #8394be;
    font-size: 12px;
    transform: rotate(0deg);
    transition: transform 0.3s ease-in-out; }
    .tu-tree-node__expand-icon.expanded {
      transform: rotate(90deg); }
    .tu-tree-node__expand-icon.is-leaf {
      color: transparent;
      cursor: default; }
  .tu-tree-node__label {
    transition: all 0.3s;
    font-size: 14px; }
  .tu-tree-node__loading-icon {
    margin-right: 8px;
    font-size: 14px;
    color: #8394be; }
  .tu-tree-node > .tu-tree-node__children {
    overflow: hidden;
    background-color: transparent; }
  .tu-tree-node.is-expanded > .tu-tree-node__children {
    display: block; }
